import React, { useState, useEffect } from "react";
import {
  Box,
  Container,
  Grid,
  TextField,
  Button,
  Typography,
  Paper,
  Link as MuiLink,
} from "@mui/material";
import EmailIcon from "@mui/icons-material/Email";
import PhoneIcon from "@mui/icons-material/Phone";
import InstagramIcon from "@mui/icons-material/Instagram";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { motion } from "framer-motion";

const Contact = () => {
  const [form, setForm] = useState({
    name: "",
    phone: "",
    altPhone: "",
    email: "",
    enquiry: "",
  });

  const [touched, setTouched] = useState({});
  const [errors, setErrors] = useState({});
  const [submitting, setSubmitting] = useState(false);

  const fadeInUp = {
    initial: { opacity: 0, y: 0 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.5 },
  };

  useEffect(() => {
    validateAllFields(form);
  }, [form]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));

    if (touched[name]) {
      validateField(name, value);
    }
  };

  const handleBlur = (e) => {
    const { name, value } = e.target;
    setTouched((prev) => ({ ...prev, [name]: true }));
    validateField(name, value);
  };

  const validateField = (name, value) => {
    let message = "";

    switch (name) {
      case "name":
        if (!value.trim()) message = "Name is required";
        break;

      case "phone":
        if (!value.trim()) {
          message = "Phone number is required";
        } else if (!/^[6-9]\d{9}$/.test(value)) {
          message = "Enter a valid 10-digit Indian mobile number";
        }
        break;

      case "email":
        if (!value.trim()) {
          message = "Email is required";
        } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
          message = "Enter a valid email";
        }
        break;

      case "enquiry":
        const wordCount = value.trim().split(/\s+/).length;
        if (!value.trim()) {
          message = "Enquiry details are required";
        } else if (wordCount > 2000) {
          message = `Must be 2000 words or fewer (currently ${wordCount})`;
        }
        break;

      default:
        break;
    }

    setErrors((prev) => ({ ...prev, [name]: message }));
  };

  const validateAllFields = (formValues) => {
    const newErrors = {};

    if (!formValues.name.trim()) newErrors.name = "Name is required";

    if (!formValues.phone.trim()) {
      newErrors.phone = "Phone number is required";
    } else if (!/^[6-9]\d{9}$/.test(formValues.phone)) {
      newErrors.phone = "Enter a valid 10-digit Indian mobile number";
    }

    if (!formValues.email.trim()) {
      newErrors.email = "Email is required";
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formValues.email)) {
      newErrors.email = "Enter a valid email";
    }

    const enquiryWords = formValues.enquiry.trim().split(/\s+/).length;
    if (!formValues.enquiry.trim()) {
      newErrors.enquiry = "Enquiry details are required";
    } else if (enquiryWords > 2000) {
      newErrors.enquiry = `Must be 2000 words or fewer (currently ${enquiryWords})`;
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    setTouched({
      name: true,
      phone: true,
      altPhone: true,
      email: true,
      enquiry: true,
    });

    const isFormValid = validateAllFields(form);
    if (!isFormValid) return;

    setSubmitting(true);

    try {
      const response = await axios.post(
        "https://yzotghl67k.execute-api.us-east-1.amazonaws.com/form-submit",
        { ...form, formType: "enquiry" },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (response.status === 200 && response.data.includes("success")) {
        toast.success("Enquiry submitted successfully!");
        setForm({ name: "", phone: "", altPhone: "", email: "", enquiry: "" });
        setTouched({});
        setErrors({});
      } else {
        toast.error("Submission failed. Please try again.");
      }
    } catch (error) {
      // console.error(error);
      toast.error("Submission failed. Please try after sometime");
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <Box sx={{ py: 6, backgroundColor: "#f9fafb", minHeight: "100vh" }}>
      <Container maxWidth="md">
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1 }}
        >
          <Typography
            variant="h4"
            fontWeight="bold"
            textAlign="center"
            gutterBottom
            sx={{ color: "#0d47a1" }}
          >
            Contact smineweb
          </Typography>
        </motion.div>
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 0.5 }} // Adds a slight delay for a staggered effect
        >
          <Typography
            variant="h6"
            align="center"
            sx={{
              mb: 4,
              color: "#555",
              fontFamily: "'Roboto', sans-serif",
              fontStyle: "italic",
            }}
          >
            Reach out to us - we'd love to hear from you!
          </Typography>
        </motion.div>

        {/* Form */}
        <motion.div {...fadeInUp} style={{ marginTop: "1rem" }}>
          <Paper elevation={2} sx={{ p: 4, mt: 2 }}>
            <Typography variant="h6" gutterBottom sx={{ color: "#004b87" }}>
              Submit your enquiry and we'll get back to you soon!
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Full Name"
                    name="name"
                    value={form.name}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={!!errors.name && touched.name}
                    helperText={touched.name && errors.name}
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Phone Number"
                    name="phone"
                    value={form.phone}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={!!errors.phone && touched.phone}
                    helperText={touched.phone && errors.phone}
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Alternate Phone"
                    name="altPhone"
                    value={form.altPhone}
                    onChange={handleChange}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Email"
                    name="email"
                    value={form.email}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={!!errors.email && touched.email}
                    helperText={touched.email && errors.email}
                    required
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Details of Enquiry"
                    name="enquiry"
                    multiline
                    rows={4}
                    value={form.enquiry}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={!!errors.enquiry && touched.enquiry}
                    helperText={touched.enquiry && errors.enquiry}
                    required
                  />
                </Grid>
                <Grid item xs={12}>
                  <Box textAlign="center">
                    <Button
                      variant="contained"
                      size="large"
                      type="submit"
                      disabled={submitting}
                    >
                      {submitting ? "Submitting..." : "Submit Enquiry"}
                    </Button>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </Paper>
          <Paper
            elevation={0}
            sx={{
              p: 3,
              mt: 6,
            }}
          >
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6} md={3}>
                <Box display="flex" alignItems="center">
                  <EmailIcon sx={{ color: "#1976d2", mr: 1 }} />
                  <MuiLink
                    href="mailto:info@sminewebtech.in"
                    underline="hover"
                    color="inherit"
                  >
                    info@sminewebtech.in
                  </MuiLink>
                </Box>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Box display="flex" alignItems="center">
                  <PhoneIcon sx={{ color: "#1976d2", mr: 1 }} />
                  <MuiLink
                    href="tel:9786413308"
                    underline="hover"
                    color="inherit"
                  >
                    +91 97864 13308
                  </MuiLink>
                </Box>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Box display="flex" alignItems="center">
                  <InstagramIcon sx={{ color: "#E1306C", mr: 1 }} />
                  <MuiLink
                    href="https://instagram.com/sminewebtech_official"
                    target="_blank"
                    underline="hover"
                    color="inherit"
                  >
                    @sminewebtech
                  </MuiLink>
                </Box>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Box display="flex" alignItems="center">
                  <LinkedInIcon sx={{ color: "#0077B5", mr: 1 }} />
                  <MuiLink
                    href="https://www.linkedin.com/in/smineweb-technologies-910340373"
                    target="_blank"
                    underline="hover"
                    color="inherit"
                  >
                    sminewebtech
                  </MuiLink>
                </Box>
              </Grid>
            </Grid>
          </Paper>
        </motion.div>
      </Container>
      <ToastContainer position="top-right" autoClose={3000} />
    </Box>
  );
};

export default Contact;
