import React from "react";
import { Box, Typography, Container, Grid, Paper } from "@mui/material";
import { motion } from "framer-motion";
import {
  CheckCircle,
  Business,
  VerifiedUser,
  Payment,
  Alarm,
  Devices,
} from "@mui/icons-material";

const reasons = [
  {
    title: "3+ Years of Experience",
    description:
      "We have over three years of experience in delivering top-notch tech solutions.",
    icon: <Business sx={{ fontSize: 40, color: "#ff5722" }} />, // Custom color for icon
  },
  {
    title: "Trusted by Small Business Owners",
    description:
      "Our clients trust us for quality service and on-time deliveries.",
    icon: <VerifiedUser sx={{ fontSize: 40, color: "#4caf50" }} />, // Custom color for icon
  },
  {
    title: "Affordable Packages",
    description:
      "Get the best value for your money with our competitive pricing.",
    icon: <Payment sx={{ fontSize: 40, color: "#03a9f4" }} />, // Custom color for icon
  },
  {
    title: "100% Satisfaction Guarantee",
    description: "We ensure customer satisfaction with every project.",
    icon: <CheckCircle sx={{ fontSize: 40, color: "#8bc34a" }} />, // Custom color for icon
  },
  {
    title: "Timely Delivery",
    description: "We value your time and deliver on schedule, every time.",
    icon: <Alarm sx={{ fontSize: 40, color: "#ff9800" }} />, // Custom color for icon
  },
  {
    title: "Cutting-Edge Technology",
    description:
      "We use the latest technologies to provide innovative solutions.",
    icon: <Devices sx={{ fontSize: 40, color: "#9c27b0" }} />, // Custom color for icon
  },
];

const WhyChooseSMINEWEB = () => {
  return (
    <Box sx={{ py: 8, backgroundColor: "#f4f7fb" }}>
      <Container>
        {/* <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1 }}
        >
          <Typography
            variant="h3"
            align="center"
            sx={{
              fontWeight: 600,
              background: "linear-gradient(90deg, #1e3c72, #2a5298)",
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
              mb: 6,
              fontFamily: "'Poppins', sans-serif",
            }}
          >
            Why Choose SMINEWEB
          </Typography>
        </motion.div> */}

        <Grid container spacing={4}>
          {reasons.map((reason, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <motion.div
                initial={{ opacity: 0, y: 40 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: index * 0.2 }}
              >
                <Paper
                  sx={{
                    p: 4,
                    borderRadius: 2,
                    backgroundColor: "#ffffff",
                    color: "#333",
                    height: "100%",
                    boxShadow: "0 4px 20px rgba(0, 0, 0, 0.1)",
                    textAlign: "center",
                    transition: "transform 0.3s, box-shadow 0.3s",
                    "&:hover": {
                      transform: "translateY(-10px)",
                      boxShadow: "0 15px 30px rgba(0, 0, 0, 0.15)",
                    },
                  }}
                >
                  {/* Icon */}
                  <Box
                    sx={{
                      mb: 2,
                      p: 2,
                      borderRadius: "50%",
                      backgroundColor: "rgba(0,0,0,0.05)",
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      width: 30, // Adjusted for a more centered icon
                      height: 30, // Adjusted for a more centered icon
                      margin: "0 auto", // Center the icon horizontally
                    }}
                  >
                    {reason.icon}
                  </Box>

                  {/* Title */}
                  <Typography
                    variant="h6"
                    sx={{
                      fontWeight: "bold",
                      color: "#1e3c72",
                      mb: 2,
                      fontFamily: "'Roboto', sans-serif",
                      fontSize: "1.1rem",
                    }}
                  >
                    {reason.title}
                  </Typography>

                  {/* Description */}
                  <Typography
                    variant="body2"
                    sx={{
                      color: "#636e72",
                      fontFamily: "'Roboto', sans-serif",
                      textAlign: "center",
                      fontSize: "0.9rem",
                    }}
                  >
                    {reason.description}
                  </Typography>
                </Paper>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default WhyChooseSMINEWEB;
