import React from "react";
import { Box, Typography, Container, Paper, Grid } from "@mui/material";
import { motion } from "framer-motion";
import {
  PersonAdd,
  DesignServices,
  Gavel,
  SupportAgent,
  TrendingUp,
} from "@mui/icons-material";

const processSteps = [
  {
    title: "Understand Your Needs",
    description:
      "We take the time to understand your unique needs before starting any project.",
    icon: <PersonAdd sx={{ fontSize: 40, color: "#D32F2F" }} />,
  },
  {
    title: "Design & Develop",
    description:
      "Our team designs and develops solutions tailored to your goals.",
    icon: <DesignServices sx={{ fontSize: 40, color: "#1976D2" }} />,
  },
  {
    title: "Test & Launch",
    description:
      "We rigorously test our solutions and launch them to ensure they meet your expectations.",
    icon: <Gavel sx={{ fontSize: 40, color: "#388E3C" }} />,
  },
  {
    title: "Support & Maintain",
    description:
      "We provide support and maintenance to keep your solution up-to-date.",
    icon: <SupportAgent sx={{ fontSize: 40, color: "#0288D1" }} />,
  },
  {
    title: "Grow with Marketing",
    description:
      "We help your business grow with targeted marketing strategies that drive results.",
    icon: <TrendingUp sx={{ fontSize: 40, color: "#8E24AA" }} />,
  },
];

const OurProcess = () => {
  return (
    <Box sx={{ py: 6, backgroundColor: "#f2fafa" }}>
      <Container>
        <Grid
          container
          direction={{ xs: "column", sm: "row" }} // Stack on small screens, row on larger
          justifyContent="center"
          alignItems="center"
          spacing={4}
        >
          {processSteps.map((step, index) => (
            <Grid
              item
              xs={12}
              sm={6}
              md={4} // Adjusted to 4 to match the width from WhyChooseSMINEWEB
              key={index}
            >
              {/* Animated Card */}
              <motion.div
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.7, delay: index * 0.3 }}
              >
                <Paper
                  sx={{
                    p: 4,
                    height: "100%", // Set height to 100% to fill available space
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "flex-start",
                    borderRadius: 4,
                    backgroundColor: "#ffffff",
                    boxShadow: 4,
                    transition: "transform 0.3s, box-shadow 0.3s",
                    "&:hover": {
                      transform: "translateY(-10px)",
                      boxShadow: "0px 15px 30px rgba(0, 0, 0, 0.15)",
                    },
                  }}
                >
                  {/* Icon */}
                  <Box
                    sx={{
                      mb: 2,
                      p: 2,
                      borderRadius: "50%",
                      backgroundColor: "rgba(0,0,0,0.05)",
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    {step.icon}
                  </Box>

                  {/* Title */}
                  <Typography
                    variant="h6"
                    sx={{
                      fontWeight: "bold",
                      color: "#1e3c72",
                      mb: 1,
                      fontFamily: "'Roboto', sans-serif",
                      textAlign: "center",
                      fontSize: "1rem",
                    }}
                  >
                    {step.title}
                  </Typography>

                  {/* Description */}
                  <Typography
                    variant="body2"
                    sx={{
                      color: "#636e72",
                      fontFamily: "'Roboto', sans-serif",
                      textAlign: "center",
                      fontSize: "0.9rem",
                      mb: 0, // Remove bottom margin to reduce empty space
                    }}
                  >
                    {step.description}
                  </Typography>
                </Paper>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default OurProcess;
