import React, { useState } from "react";
import { motion } from "framer-motion";
import {
  Box,
  Container,
  TextField,
  Grid,
  Typography,
  Button,
  Paper,
  Tabs,
  Tab,
} from "@mui/material";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Career = () => {
  const [activeTab, setActiveTab] = useState("internship");
  const [submitting, setSubmitting] = useState(false);

  const fadeInUp = {
    initial: { opacity: 0, y: 0 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.5 },
  };

  const tabs = [
    { key: "internship", label: "Internship" },
    { key: "openings", label: "Openings" },
  ];

  const MAX_FILE_SIZE = 3 * 1024 * 1024; // 3MB

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSubmitting(true);

    const form = e.target;
    const fileInput = form.resume;
    const reasonText = form.reasonToJoin?.value?.trim() || "";

    // Check resume
    if (fileInput.files.length === 0) {
      toast.error("Please upload your resume.");
      setSubmitting(false);
      return;
    }

    const file = fileInput.files[0];
    if (file.size > MAX_FILE_SIZE) {
      toast.error("Resume file size must be under 3MB.");
      setSubmitting(false);
      return;
    }

    // Check word limit for reasonToJoin
    const wordCount = reasonText.split(/\s+/).filter(Boolean).length;
    if (wordCount > 1000) {
      toast.error("The reason should not exceed 1000 words.");
      setSubmitting(false);
      return;
    }

    const formData = new FormData(form);
    formData.append("formType", "career");

    try {
      const response = await axios.post(
        "https://yzotghl67k.execute-api.us-east-1.amazonaws.com/form-submit",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );

      if (response.status === 200 && response.data.includes("success")) {
        toast.success("Application submitted successfully!");
        form.reset();
      } else {
        toast.error("Submission failed. Please try again.");
      }
    } catch (error) {
      console.error(error);
      toast.error("Submission failed. Please try again later ");
    } finally {
      setSubmitting(false);
    }
  };

  const renderSection = () => {
    switch (activeTab) {
      case "internship":
        return (
          <Paper elevation={2} sx={{ p: 4, mt: 2 }}>
            <Typography variant="h6" gutterBottom sx={{ color: "#004b87" }}>
              Submit the form and we'll get back to you soon!
            </Typography>
            <Typography variant="body2" mb={3} color="text.secondary">
              We're always looking for enthusiastic interns ready to learn and
              grow with us. Fill out the form below to apply.
            </Typography>

            <Box
              component="form"
              onSubmit={handleSubmit}
              encType="multipart/form-data"
            >
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <TextField fullWidth label="Full Name" name="name" required />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Email Address"
                    name="email"
                    type="email"
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Phone Number"
                    name="phone"
                    type="tel"
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Education / Degree"
                    name="education"
                    required
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Why do you want to join our internship?"
                    name="reasonToJoin"
                    multiline
                    rows={4}
                    required
                    inputProps={{ maxLength: 1000 }}
                    helperText="Include a maximum of 1000 characters"
                  />
                </Grid>
                <Grid item xs={12}>
                  <Typography mb={1}>Upload Resume:</Typography>
                  <input
                    type="file"
                    name="resume"
                    // accept=".pdf,.doc,.docx"
                    required
                  />
                </Grid>
                <Grid item xs={12}>
                  <Box textAlign="center">
                    <Button
                      variant="contained"
                      size="large"
                      type="submit"
                      disabled={submitting}
                    >
                      {submitting ? "Submitting..." : "Submit Application"}
                    </Button>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </Paper>
        );
      case "openings":
        return (
          <Box mt={2}>
            <Paper elevation={2} sx={{ p: 4 }}>
              <Typography variant="h6" gutterBottom sx={{ color: "#004b87" }}>
                Job Openings
              </Typography>
              <Typography variant="body1" color="text.secondary">
                Currently, there are no job openings. Please check back later.
              </Typography>
            </Paper>
          </Box>
        );
      default:
        return null;
    }
  };

  return (
    <Box sx={{ py: 6, backgroundColor: "#f9fafb", minHeight: "100vh" }}>
      <Container maxWidth="md">
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1 }}
        >
          <Typography
            variant="h4"
            fontWeight="bold"
            textAlign="center"
            gutterBottom
            sx={{ color: "#0d47a1" }}
          >
            Career opportunities at smineweb
          </Typography>
        </motion.div>

        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 0.5 }} // Adds a slight delay for a staggered effect
        >
          <Typography
            variant="h6"
            align="center"
            sx={{
              mb: 4,
              color: "#555",
              fontFamily: "'Roboto', sans-serif",
              fontStyle: "italic",
            }}
          >
            Join our growing team of passionate developers, designers, and
            innovators!
          </Typography>
        </motion.div>

        {/* <AnimatePresence mode="wait"> */}
        <motion.div key={activeTab} {...fadeInUp} style={{ marginTop: "1rem" }}>
          <Box
            sx={{
              ...styles.tabContainer,
              backgroundColor: "#fff", // Background color for tabs
              transition: "background-color 0.3s ease",
            }}
          >
            <Tabs
              value={tabs.findIndex((tab) => tab.key === activeTab)}
              onChange={(e, newValue) => setActiveTab(tabs[newValue].key)}
              centered
              textColor="primary"
              indicatorColor="primary"
              variant="scrollable"
              scrollButtons="auto"
            >
              {tabs.map((tab) => (
                <Tab
                  key={tab.key}
                  label={
                    <Box sx={{ display: "flex", alignItems: "center" }}>
                      {tab.label}
                      {/* {activeTab !== tab.key && (
                      <ArrowForwardIosIcon
                        style={{
                          fontSize: "16px",
                          marginLeft: "6px",
                          verticalAlign: "middle",
                        }}
                      />
                    )} */}
                    </Box>
                  }
                />
              ))}
            </Tabs>
          </Box>
          {renderSection()}
        </motion.div>
        {/* </AnimatePresence> */}
      </Container>
      <ToastContainer position="top-right" autoClose={3000} />
    </Box>
  );
};

const styles = {
  tabContainer: {
    display: "flex",
    justifyContent: "center",
    gap: "2rem",
    flexWrap: "wrap",
    padding: "1rem",
  },
};

export default Career;
