import React from "react";

const RefundPolicy = () => {
  return (
    <section>
      {/* <h2>Refund Policy</h2> */}
      <p>
        You understand that upon initiating a transaction to avail of the
        Services, you are entering into a legally binding and enforceable
        contract with us for the Services.
      </p>
      <p>
        You shall be entitled to claim a refund of the payment made by you in
        case we are unable to provide the Service. The timelines for such return
        and refund will be according to the specific Service you have availed of
        or within the time period provided in our policies (as applicable). If
        you do not raise a refund claim within the stipulated time, you will
        become ineligible for a refund.
      </p>
    </section>
  );
};

export default RefundPolicy;
