import React, { useState } from "react";
import {
  Box,
  Container,
  Typography,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  IconButton,
  Button,
  Stack,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import PhoneIcon from "@mui/icons-material/Phone";
import EmailIcon from "@mui/icons-material/Email";
import InstagramIcon from "@mui/icons-material/Instagram";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
// import LocationOnIcon from "@mui/icons-material/LocationOn";

import RefundPolicy from "../../pages/refundPolicy";
import PrivacyPolicy from "../../pages/privacyPolicy";
import TermsAndConditions from "../../pages/termsAndConditions";

const Footer = () => {
  const policyLinks = [
    { title: "Privacy Policy", component: <PrivacyPolicy /> },
    { title: "Terms & Conditions", component: <TermsAndConditions /> },
    { title: "Refund Policy", component: <RefundPolicy /> },
  ];

  const [dialogTitle, setDialogTitle] = useState("");
  const [dialogContent, setDialogContent] = useState(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const openDialog = (title, content) => {
    setDialogTitle(title);
    setDialogContent(content);
    setIsDialogOpen(true);
  };

  const closeDialog = () => {
    setIsDialogOpen(false);
  };

  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: "#0a2a4d",
        color: "white",
        py: 5,
      }}
    >
      <Container maxWidth="lg">
        <Box textAlign="center" mb={3}>
          <Stack
            direction="row"
            spacing={1.5}
            justifyContent="center"
            alignItems="center"
            sx={{ mb: 2 }}
          >
            <IconButton
              onClick={() =>
                window.open("https://wa.me/919786413308", "_blank")
              }
            >
              <WhatsAppIcon sx={{ fontSize: 24, color: "#25D366" }} />
            </IconButton>

            <IconButton
              onClick={() => window.open("tel:+919786413308", "_blank")}
            >
              <PhoneIcon sx={{ fontSize: 24, color: "white" }} />
            </IconButton>

            <IconButton
              onClick={() =>
                window.open("mailto:info@sminewebtech.in", "_blank")
              }
            >
              <EmailIcon sx={{ fontSize: 24, color: "#EA4335" }} />
            </IconButton>

            <IconButton
              onClick={() =>
                window.open(
                  "https://www.instagram.com/sminewebtech_official",
                  "_blank"
                )
              }
            >
              <InstagramIcon sx={{ fontSize: 24, color: "#E4405F" }} />
            </IconButton>

            <IconButton
              onClick={() =>
                window.open(
                  "https://www.linkedin.com/in/smineweb-technologies-910340373",
                  "_blank"
                )
              }
            >
              <LinkedInIcon sx={{ fontSize: 24, color: "#0077b5" }} />
            </IconButton>

            {/* <IconButton
              onClick={() =>
                window.open(
                  "https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3899.433142256146!2d79.06843188218389!3d12.218924457171696!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bacbfc9b5bc08b3%3A0x7f596ecfbb0d8da3!2sSMINESERVICES!5e0!3m2!1sen!2sin!4v1746444712706!5m2!1sen!2sin",
                  "_blank"
                )
              }
            >
              <LocationOnIcon sx={{ fontSize: 24, color: "#4285F4" }} />
            </IconButton> */}
          </Stack>
        </Box>

        <Divider sx={{ mb: 3, borderColor: "rgba(255, 255, 255, 0.3)" }} />

        <Box textAlign="center">
          <Stack
            direction="row"
            spacing={1.5}
            justifyContent="center"
            alignItems="center"
            flexWrap="wrap"
            sx={{ mb: 1 }}
          >
            {policyLinks.map((policy) => (
              <Button
                key={policy.title}
                onClick={() => openDialog(policy.title, policy.component)}
                sx={{
                  color: "rgba(255, 255, 255, 0.7)",
                  fontSize: "13px",
                  textTransform: "none",
                  px: 1,
                  "&:hover": {
                    color: "#fff",
                    textDecoration: "underline",
                  },
                }}
              >
                {policy.title}
              </Button>
            ))}
          </Stack>
          <Typography
            variant="body2"
            textAlign="center"
            sx={{
              color: "rgba(255, 255, 255, 0.6)",
              fontSize: { xs: "12px", sm: "13px" },
            }}
          >
             {new Date().getFullYear()} smineweb. All rights reserved.
          </Typography>
        </Box>
      </Container>

      {/* Dialog for policy content */}
      <Dialog
        open={isDialogOpen}
        onClose={closeDialog}
        fullWidth
        maxWidth="sm"
        TransitionProps={{
          onExited: () => {
            setDialogContent(null);
            setDialogTitle("");
          },
        }}
      >
        <DialogTitle
          sx={{
            backgroundColor: "#0a2a4d",
            color: "white",
            fontWeight: "bold",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          {dialogTitle}
          <IconButton onClick={closeDialog} sx={{ color: "white" }}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent
          dividers
          sx={{
            backgroundColor: "#f7f7f7",
            color: "#333",
            padding: "24px",
          }}
        >
          {dialogContent}
        </DialogContent>
      </Dialog>
    </Box>
  );
};

export default Footer;
