import React from "react";

const PrivacyPolicy = () => {
  return (
    <div className="privacy-policy">
      <section>
        <p>
          This Privacy Policy outlines how we collect, use, and protect your
          personal information when you visit our website.
        </p>
      </section>

      <section aria-labelledby="info-collect">
        <h3 id="info-collect">Information We Collect</h3>
        <p>
          When you visit our website, we may collect personal information such
          as your name, email address, phone number, and company details if you
          choose to provide them to us through forms or inquiries.
        </p>
        <p>
          We may also collect non-personal information such as your IP address,
          browser type, operating system, and browsing behavior on our website
          through cookies and similar technologies.
        </p>
      </section>

      <section aria-labelledby="use-of-info">
        <h3 id="use-of-info">Use of Information</h3>
        <p>
          We use the personal information you provide to respond to your
          inquiries, process your requests, and communicate with you about our
          services.
        </p>
        <p>
          Non-personal information may be used to analyze website traffic,
          improve our website, and customize your browsing experience.
        </p>
      </section>

      <section aria-labelledby="data-sharing">
        <h3 id="data-sharing">Data Sharing</h3>
        <p>
          We do not sell, trade, or rent your personal information to third
          parties. We may share your personal information with trusted
          third-party service providers who assist us in operating our website
          or conducting our business, provided they agree to keep your
          information confidential.
        </p>
      </section>

      <section aria-labelledby="data-security">
        <h3 id="data-security">Data Security</h3>
        <p>
          We take appropriate measures to protect your personal information from
          unauthorized access, alteration, disclosure, or destruction. However,
          please note that no method of transmission over the internet or
          electronic storage is 100% secure, and we cannot guarantee absolute
          security.
        </p>
      </section>

      <section aria-labelledby="third-party-sites">
        <h3 id="third-party-sites">Third-Party Websites</h3>
        <p>
          Our website may contain links to third-party websites. We are not
          responsible for the privacy practices or content of these websites. We
          encourage you to review the privacy policies of these third-party
          sites before providing any personal information.
        </p>
      </section>

      <section aria-labelledby="updates">
        <h3 id="updates">Updates</h3>
        <p>
          We reserve the right to update or modify this Privacy Policy at any
          time without prior notice. Any changes will be effective immediately
          upon posting on this page.
        </p>
      </section>

      <section aria-labelledby="consent">
        <h3 id="consent">Your Consent</h3>
        <p>
          By using our website, you consent to the collection, use, and
          disclosure of your information as described in this Privacy Policy.
        </p>
      </section>

      <section aria-labelledby="contact-us">
        <h3 id="contact-us">Contact Us</h3>
        <p>
          If you have any questions or concerns about this Privacy Policy,
          please contact us at{" "}
          <a href="mailto:info@sminewebtech.in">
            info@sminewebtech.in
          </a>
          .
        </p>
      </section>
    </div>
  );
};

export default PrivacyPolicy;
