import React from "react";
import { Box, Typography, Grid, Paper } from "@mui/material";
import {
  Language as LanguageIcon,
  PhoneIphone as PhoneIphoneIcon,
  Campaign as CampaignIcon,
  Public as PublicIcon,
  ShoppingCart as ShoppingCartIcon,
} from "@mui/icons-material";
import { motion } from "framer-motion";

const services = [
  {
    title: "Web Design & Development",
    icon: (
      <LanguageIcon
        fontSize="large"
        sx={{ color: "#4dabf5" }} // Light blue
      />
    ),
    description:
      "Creating visually stunning websites that are user-friendly and mobile responsive.",
  },
  {
    title: "Mobile App Development",
    icon: (
      <PhoneIphoneIcon
        fontSize="large"
        sx={{ color: "#66bb6a" }} // Green
      />
    ),
    description:
      "Building robust and scalable mobile applications for both Android and iOS platforms.",
  },
  {
    title: "Digital Marketing",
    icon: (
      <CampaignIcon
        fontSize="large"
        sx={{ color: "#ff7043" }} // Orange
      />
    ),
    description:
      "Helping businesses grow with targeted digital marketing strategies across various platforms.",
  },
  {
    title: "Web Applications",
    icon: (
      <PublicIcon
        fontSize="large"
        sx={{ color: "#ba68c8" }} // Purple
      />
    ),
    description:
      "Developing dynamic, high-performance web applications to streamline your business operations.",
  },
  {
    title: "E-commerce Solutions",
    icon: (
      <ShoppingCartIcon
        fontSize="large"
        sx={{ color: "#fdd835" }} // Yellow
      />
    ),
    description:
      "Delivering complete e-commerce solutions that are secure, scalable, and tailored to your business needs.",
  },
];

const itemVariants = {
  hidden: { opacity: 0, y: 30 },
  visible: (i) => ({
    opacity: 1,
    y: 0,
    transition: {
      delay: i * 0.3,
      type: "spring", // Smooth spring effect
      stiffness: 100,
      damping: 20,
    },
  }),
};

const ServicesSection = () => {
  return (
    <Box
      sx={{
        py: 8,
        px: 4,
        backgroundColor: "#f4f7fb",
        textAlign: "center",
        color: "#333", // Dark text color for better contrast
      }}
    >
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
      >
        <Typography
          variant="h4"
          fontWeight="bold"
          sx={{
            mb: 4,
            color: "#0d47a1", // Deep blue for professionalism and trust
            textShadow: "1px 1px 2px rgba(0, 0, 0, 0.1)",
          }}
        >
          Innovative digital solutions for your business
        </Typography>
      </motion.div>

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1, delay: 0.5 }} // Adds a slight delay for a staggered effect
      >
        <Typography
          variant="h6"
          sx={{
            mb: 6,
            color: "#424242", // Darker gray for elegance
            textShadow: "none",
            fontFamily: "'Roboto', sans-serif",
            fontStyle: "italic",
          }}
        >
          Discover our core offerings that can help your business thrive
        </Typography>
      </motion.div>

      <Grid container spacing={4} justifyContent="center">
        {services.map((service, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <motion.div
              custom={index}
              initial="hidden"
              whileInView="visible"
              viewport={{ once: true }}
              variants={itemVariants}
            >
              <Paper
                elevation={6} // Added slightly stronger elevation for better shadow
                sx={{
                  p: 4,
                  borderRadius: 3,
                  backgroundColor: "#ffffff",
                  color: "#333",
                  boxShadow: "0 8px 16px rgba(0,0,0,0.1)", // Updated shadow for depth
                  transition: "transform 0.3s ease, box-shadow 0.3s ease",
                  "&:hover": {
                    transform: "translateY(-10px)",
                    boxShadow: "0 16px 32px rgba(0,0,0,0.2)", // Enhanced hover effect
                  },
                }}
              >
                <Box
                  sx={{
                    mb: 2,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    width: 72,
                    height: 72,
                    borderRadius: "50%",
                    backgroundColor: "#f5f5f5", // Light gray background
                    mx: "auto",
                  }}
                >
                  {service.icon}
                </Box>

                <Typography
                  variant="h6"
                  fontWeight="600"
                  sx={{
                    mb: 1,
                    color: "#333", // Darker color for the service title to differentiate
                    fontSize: "1.125rem", // Slightly larger title font
                  }}
                >
                  {service.title}
                </Typography>
                <Typography
                  variant="body2"
                  sx={{
                    color: "#777",
                    fontSize: "0.875rem", // Slightly smaller description font for readability
                    textAlign: "center",
                    lineHeight: "1.5", // Adjusted line height for better spacing
                  }}
                >
                  {service.description}
                </Typography>
              </Paper>
            </motion.div>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default ServicesSection;
