import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import {
  AppBar,
  Box,
  Toolbar,
  IconButton,
  Typography,
  Menu,
  MenuItem,
  Container,
  Button,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import logo from "../../assets/smineweb.png";

const Header = () => {
  const [anchorElNav, setAnchorElNav] = useState(null);
  const location = useLocation();

  const handleOpenNavMenu = (event) => setAnchorElNav(event.currentTarget);
  const handleCloseNavMenu = () => setAnchorElNav(null);

  const pages = [
    { name: "Home", path: "/" },
    { name: "About", path: "/about" },
    { name: "Services", path: "/services" },
    { name: "Pricing", path: "/pricing" },
    // { name: "Portfolio", path: "/portfolio" },
    { name: "Careers", path: "/careers" },
    // { name: "Internship", path: "/internship" },
    { name: "Contact Us", path: "/contact" },
  ];

  return (
    <AppBar
      position="sticky"
      sx={{
        backgroundColor: "#fff",
        boxShadow: "0px 2px 8px rgba(0, 0, 0, 0.1)",
        zIndex: 1100,
      }}
    >
      <Container maxWidth="lg">
        <Toolbar disableGutters>
          {/* Logo and Brand */}
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              flexGrow: 1,
            }}
          >
            <Box
              component={Link}
              to="/"
              sx={{
                display: "flex",
                alignItems: "center",
                textDecoration: "none",
              }}
            >
              <Box
                component="img"
                src={logo}
                alt="smineweb Logo"
                sx={{ height: 50, mr: 2 }}
              />
              {/* <Box>
                <Typography
                  variant="h6"
                  sx={{
                    fontWeight: "bold",
                    color: "#1e88e5",
                    lineHeight: 1.1,
                  }}
                >
                  smineweb
                </Typography>
                <Typography
                  variant="caption"
                  sx={{ color: "#777", fontSize: "0.75rem" }}
                >
                  Innovate. Build. Scale.
                </Typography>
              </Box> */}
            </Box>
          </Box>

          {/* Mobile Menu */}
          <Box sx={{ display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              onClick={handleOpenNavMenu}
              aria-label="menu"
              sx={{ color: "#1e88e5" }}
            >
              <MenuIcon />
            </IconButton>
            <Menu
              anchorEl={anchorElNav}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                "& .MuiPaper-root": {
                  mt: 1,
                  borderRadius: 2,
                  minWidth: 180,
                  boxShadow: "0px 8px 24px rgba(0,0,0,0.1)",
                },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page.name} onClick={handleCloseNavMenu}>
                  <Typography
                    component={Link}
                    to={page.path}
                    sx={{
                      textDecoration: "none",
                      color:
                        location.pathname === page.path ? "#1e88e5" : "#333",
                      fontWeight:
                        location.pathname === page.path ? "bold" : "normal",
                    }}
                  >
                    {page.name}
                  </Typography>
                </MenuItem>
              ))}
              {/* <MenuItem onClick={handleCloseNavMenu}>
                <Typography
                  component={Link}
                  to="/contact"
                  sx={{
                    color: "#1e88e5",
                    textDecoration: "none",
                    fontWeight: "bold",
                  }}
                >
                  Contact Us
                </Typography>
              </MenuItem> */}
            </Menu>
          </Box>

          {/* Desktop Menu */}
          <Box
            sx={{
              display: { xs: "none", md: "flex" },
              alignItems: "center",
              gap: 1.5,
            }}
          >
            {pages.map((page) => (
              <Button
                key={page.name}
                component={Link}
                to={page.path}
                sx={{
                  color: location.pathname === page.path ? "#1e88e5" : "#333",
                  fontWeight:
                    location.pathname === page.path ? "bold" : "medium",
                  "&:hover": {
                    color: "#1976d2",
                  },
                }}
              >
                {page.name}
              </Button>
            ))}

            {/* <Button
              component={Link}
              to="/contact"
              variant="contained"
              sx={{
                backgroundColor: "#1e88e5",
                color: "#fff",
                fontWeight: "bold",
                textTransform: "none",
                borderRadius: 2,
                ml: 1,
                "&:hover": {
                  backgroundColor: "#1565c0",
                },
              }}
            >
              Contact Us
            </Button> */}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;
