import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Box, Tabs, Tab } from "@mui/material";
// import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import HeroSection from "./herosection";
import IndustriesWeServe from "./industriesWeServe";
import OurProcess from "./ourProcess";
import WhyChooseSection from "./whyChooseSection";

const Home = () => {
  const [activeSection, setActiveSection] = useState("why");

  const tabs = [
    { key: "why", label: "Why smineweb" },
    { key: "process", label: "Our process" },
    { key: "industries", label: "Industries we serve" },
  ];

  const renderSection = () => {
    switch (activeSection) {
      case "why":
        return <WhyChooseSection />;
      case "process":
        return <OurProcess />;
      case "industries":
        return <IndustriesWeServe />;
      default:
        return null;
    }
  };

  const fadeInUp = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.5 },
  };

  // Set background color dynamically based on the active section
  const getSectionBackgroundColor = (section) => {
    switch (section) {
      case "why":
        return "#f4f7fb"; // Background for 'Why Choose'
      case "process":
        return "#f2fafa"; // Background for 'Our Process'
      case "industries":
        return "#f0fdf9"; // Background for 'Industries We Serve'
      default:
        return "#fff";
    }
  };

  return (
    <>
      <HeroSection />

      <Box
        sx={{
          ...styles.tabContainer,
          backgroundColor: getSectionBackgroundColor(activeSection),
          transition: "background-color 0.3s ease",
        }}
      >
        <Tabs
          value={tabs.findIndex((tab) => tab.key === activeSection)}
          onChange={(e, newValue) => setActiveSection(tabs[newValue].key)}
          centered
          textColor="primary"
          indicatorColor="primary"
          variant="scrollable" // Enables horizontal scroll
          scrollButtons="auto" // Shows arrows if needed
          sx={{ mb: 3 }}
        >
          {tabs.map((tab) => (
            <Tab
              key={tab.key}
              label={
                <Box sx={{ display: "flex", alignItems: "center" }}>
                  {tab.label}
                  {/* {activeSection !== tab.key && (
                    <ArrowForwardIosIcon
                      style={{
                        fontSize: "16px",
                        marginLeft: "6px",
                        verticalAlign: "middle",
                      }}
                    />
                  )} */}
                </Box>
              }
            />
          ))}
        </Tabs>
      </Box>

      <div
        style={{
          padding: "0 1rem",
          marginTop: "0",
          backgroundColor: getSectionBackgroundColor(activeSection),
          transition: "background-color 0.3s ease",
        }}
      >
        <AnimatePresence mode="wait">
          <motion.div
            key={activeSection}
            {...fadeInUp}
            style={{
              minHeight: "auto",
              marginTop: "0",
            }}
          >
            {renderSection()}
          </motion.div>
        </AnimatePresence>
      </div>
    </>
  );
};

const styles = {
  tabContainer: {
    display: "flex",
    justifyContent: "center",
    gap: "2rem",
    marginTop: "0",
    paddingBottom: "0.5rem",
    flexWrap: "wrap",
    padding: "1rem",
    marginBottom: "0",
  },
};

export default Home;
