import { useState } from "react";
import {
  Box,
  Typography,
  Grid,
  Paper,
  Container,
  Tabs,
  Tab,
} from "@mui/material";
import { motion, AnimatePresence } from "framer-motion";

const AboutUs = () => {
  const [activeSection, setActiveSection] = useState("vision");

  const tabs = [
    { key: "vision", label: "Vision" },
    { key: "mission", label: "Mission" },
  ];

  const renderSection = () => {
    switch (activeSection) {
      case "vision":
        return (
          <Typography variant="h6" sx={{ color: "#555", fontStyle: "italic" }}>
            Empowering small businesses through technology, creativity, and
            innovation.
          </Typography>
        );
      case "mission":
        return (
          <Typography variant="h6" sx={{ color: "#555", fontStyle: "italic" }}>
            To provide high-quality web and digital solutions that drive growth
            and build lasting relationships.
          </Typography>
        );
      default:
        return null;
    }
  };

  const fadeInUp = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.5 },
  };

  return (
    <Box
      sx={{
        py: 8,
        px: 4,
        backgroundColor: "#f4f7fb",
        textAlign: "center",
        color: "#333",
      }}
    >
      <Container>
        {/* <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1 }}
        >
          <Typography
            variant="h3"
            fontWeight="bold"
            sx={{
              mb: 6,
              color: "#1e3c72",
              textShadow: "2px 2px 4px rgba(0, 0, 0, 0.2)",
              fontFamily: "'Poppins', sans-serif",
            }}
          >
            About SMINEWEB
          </Typography>
        </motion.div> */}

        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 0.5 }} // Adds a slight delay for a staggered effect
        >
          <Typography
            variant="h6"
            sx={{
              mb: 6,
              color: "#555",
              fontFamily: "'Roboto', sans-serif",
              fontStyle: "italic",
            }}
          >
            SMINEWEB Technologies was founded in 2024 in Tamil Nadu with a vision to provide
            innovative and affordable webtech solutions to small businesses.
          </Typography>
          <Box
            sx={{
              ...styles.tabContainer,
              transition: "background-color 0.3s ease",
            }}
          >
            <Tabs
              value={tabs.findIndex((tab) => tab.key === activeSection)}
              onChange={(e, newValue) => setActiveSection(tabs[newValue].key)}
              centered
              textColor="primary"
              indicatorColor="primary"
              variant="scrollable"
              scrollButtons="auto"
              sx={{ mb: 4 }}
            >
              {tabs.map((tab) => (
                <Tab key={tab.key} label={tab.label} />
              ))}
            </Tabs>
          </Box>
          <AnimatePresence mode="wait">
            <motion.div key={activeSection} {...fadeInUp}>
              {renderSection()}
            </motion.div>
          </AnimatePresence>
        </motion.div>

        {/* Motion effect added to Core Values text */}
        {/* Motion effect added to Core Values text */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, delay: 0.5 }}
          style={{ marginTop: "4rem" }} // <-- Add this line
        >
          <Typography
            variant="h5"
            sx={{
              mb: 4,
              fontWeight: "bold",
              color: "#1e3c72",
            }}
          >
            Our core values
          </Typography>
        </motion.div>

        <Grid container spacing={4} justifyContent="center">
          {["Innovation", "Trust", "Quality", "Customer Focus"].map(
            (value, index) => (
              <Grid item xs={12} sm={6} md={3} key={index}>
                <motion.div
                  initial={{ opacity: 0, y: 50 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 1, delay: 0.5 + index * 0.3 }}
                >
                  <Paper
                    sx={{
                      p: 4,
                      borderRadius: 3,
                      backgroundColor: "#ffffff",
                      boxShadow: "0 6px 12px rgba(0,0,0,0.1)",
                      transition: "transform 0.3s ease, box-shadow 0.3s ease",
                      "&:hover": {
                        transform: "translateY(-8px)",
                        boxShadow: "0 12px 24px rgba(0,0,0,0.15)",
                      },
                    }}
                  >
                    <Typography
                      variant="h6"
                      fontWeight="600"
                      sx={{
                        mb: 2,
                        color: "#1e3c72",
                      }}
                    >
                      {value}
                    </Typography>
                    <Typography
                      variant="body2"
                      sx={{
                        color: "#777",
                      }}
                    >
                      Our commitment to {value.toLowerCase()} ensures that we
                      deliver exceptional results to our clients with every
                      project.
                    </Typography>
                  </Paper>
                </motion.div>
              </Grid>
            )
          )}
        </Grid>

        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, delay: 1 }}
        >
          <Typography
            variant="h5"
            sx={{
              mt: 8,
              fontWeight: "bold",
              color: "#1e3c72",
            }}
          >
            Join Us in Making a Difference
          </Typography>
          <Typography
            variant="body1"
            sx={{
              mt: 3,
              color: "#555",
              fontFamily: "'Roboto', sans-serif",
              fontStyle: "italic",
            }}
          >
            SMINEWEB is on a mission to help small businesses grow by leveraging
            the power of technology. Our innovative solutions are designed with
            your success in mind. Join us in transforming your business with
            tailored webtech solutions that drive results.
          </Typography>
        </motion.div>
      </Container>
    </Box>
  );
};

const styles = {
  tabContainer: {
    display: "flex",
    justifyContent: "center",
    gap: "2rem",
    marginTop: "0",
    paddingBottom: "0.5rem",
    flexWrap: "wrap",
    padding: "1rem",
    marginBottom: "0",
  },
};

export default AboutUs;
