import React, { useState } from "react";
import {
  Box,
  Tabs,
  Tab,
  Typography,
  Grid,
  Card,
  CardContent,
  Button,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import { logoPlans, websitePlans } from "./plans";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";

const getTickIconColor = (planTitle, planType) => {
  switch (planType) {
    case "Website":
      switch (planTitle) {
        case "Basic plan":
          return "success";
        case "First step plan":
          return "primary";
        case "Growup plan":
          return "warning";
        case "Golden plan":
          return "error";
        case "E-Commerce plan":
          return "info";
        case "Golden e-commerce plan":
          return "secondary";
        default:
          return "success";
      }
    case "Logo":
      switch (planTitle) {
        case "Basic plan":
          return "success";
        case "Startup plan":
          return "primary";
        case "3 Combo plan":
          return "warning";
        case "5 Star plan":
          return "error";
        default:
          return "success";
      }
    default:
      return "success";
  }
};

const TabPanel = ({ children, value, index }) => {
  return (
    <div hidden={value !== index}>
      {value === index && <Box sx={{ mt: 2 }}>{children}</Box>}
    </div>
  );
};

const PlansTabSection = () => {
  const [tab, setTab] = useState(0);
  const navigate = useNavigate();

  const fadeInUp = {
    initial: { opacity: 0, y: 0 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.5 },
  };

  // Framer Motion Animation for Text
  // const headingVariants = {
  //   hidden: { opacity: 0, y: 20 },
  //   visible: { opacity: 1, y: 0, transition: { duration: 1, ease: "easeOut" } },
  // };

  return (
    <Box sx={{ py: 6, px: 3, backgroundColor: "#f4f7fb", color: "#333" }}>
      {/* Animated Heading for "Explore Our Affordable Plans" */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
      >
        <Typography
          variant="h4"
          fontWeight="bold"
          align="center"
          sx={{ mb: 2, color: "#0d47a1" }}
        >
          Explore our affordable plans
        </Typography>
      </motion.div>

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1, delay: 0.5 }} // Adds a slight delay for a staggered effect
      >
        <Typography
          variant="h6"
          align="center"
          sx={{
            mb: 4,
            color: "#555",
            fontFamily: "'Roboto', sans-serif",
            fontStyle: "italic",
          }}
        >
          Choose the package that suits your business needs best
        </Typography>
      </motion.div>

      <motion.div key={tab} {...fadeInUp} style={{ marginTop: "1rem" }}>
        <Tabs
          value={tab}
          onChange={(e, newValue) => setTab(newValue)}
          centered
          textColor="primary"
          indicatorColor="primary"
          sx={{ mb: 3 }}
        >
          <Tab label="Website Design" />
          <Tab label="Logo Design" />
        </Tabs>

        {/* Tab Panel for Website Plans */}

        <TabPanel value={tab} index={0}>
          <Box sx={{ maxWidth: 1200, mx: "auto", px: 2 }}>
            <Grid container spacing={2} justifyContent="center">
              {websitePlans.map((plan, idx) => (
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={4}
                  key={idx}
                  display="flex"
                  justifyContent="center"
                >
                  <Card
                    elevation={3}
                    sx={{
                      width: "100%",
                      maxWidth: 350,
                      display: "flex",
                      flexDirection: "column",
                    }}
                  >
                    <CardContent sx={{ flexGrow: 1, px: 2, py: 2 }}>
                      <Typography variant="h6" gutterBottom color="primary">
                        {plan.title}
                      </Typography>
                      {/* <Typography
                      // variant="h6"
                      color="text.secondary"
                      gutterBottom
                    >
                      {plan.price}
                    </Typography> */}
                      <Typography variant="subtitle2" gutterBottom>
                        {plan.type}
                      </Typography>
                      <List dense>
                        {plan.features.map((feature, i) => (
                          <ListItem key={i}>
                            <ListItemIcon>
                              <CheckCircleIcon
                                color={getTickIconColor(plan.title, "Website")}
                              />
                            </ListItemIcon>
                            <ListItemText primary={feature} />
                          </ListItem>
                        ))}
                      </List>
                    </CardContent>
                    <Box sx={{ textAlign: "center", pb: 2 }}>
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => navigate("/contact")}
                      >
                        Contact us
                      </Button>
                    </Box>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Box>
        </TabPanel>

        {/* Tab Panel for Logo Plans */}
        <TabPanel value={tab} index={1}>
          <Box sx={{ maxWidth: 1200, mx: "auto", px: 2 }}>
            <Grid container spacing={2} justifyContent="center">
              {logoPlans.map((plan, idx) => (
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={4}
                  key={idx}
                  display="flex"
                  justifyContent="center"
                >
                  <Card
                    elevation={3}
                    sx={{
                      width: "100%",
                      maxWidth: 350,
                      display: "flex",
                      flexDirection: "column",
                    }}
                  >
                    <CardContent sx={{ flexGrow: 1, px: 2, py: 2 }}>
                      <Typography variant="h6" gutterBottom color="secondary">
                        {plan.title}
                      </Typography>
                      {/* <Typography
                      // variant="h6"
                      color="text.secondary"
                      gutterBottom
                    >
                      {plan.price}
                    </Typography> */}
                      <List dense>
                        {plan.features.map((feature, i) => (
                          <ListItem key={i}>
                            <ListItemIcon>
                              <CheckCircleIcon
                                color={getTickIconColor(plan.title, "Logo")}
                              />
                            </ListItemIcon>
                            <ListItemText primary={feature} />
                          </ListItem>
                        ))}
                      </List>
                    </CardContent>
                    <Box sx={{ textAlign: "center", pb: 2 }}>
                      <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => navigate("/contact")}
                      >
                        Contact us
                      </Button>
                    </Box>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Box>
        </TabPanel>
      </motion.div>
    </Box>
  );
};

export default PlansTabSection;
