import React from "react";
import { Box, Typography, Container, Grid, Paper } from "@mui/material";
import { motion } from "framer-motion";
import {
  ShoppingCart,
  School,
  Hotel,
  Storefront,
  Business,
  LocalHospital,
} from "@mui/icons-material"; // Importing icons

const industries = [
  {
    title: "E-commerce",
    description:
      "We deliver seamless online shopping experiences with custom solutions for e-commerce businesses.",
    icon: <ShoppingCart sx={{ fontSize: 40, color: "#11998e" }} />, // Slightly smaller icon for E-commerce
  },
  {
    title: "Education",
    description:
      "Our platforms empower educational institutions to deliver high-quality digital learning.",
    icon: <School sx={{ fontSize: 40, color: "#38ef7d" }} />, // Slightly smaller icon for Education
  },
  {
    title: "Hospitality",
    description:
      "We provide smart solutions for the hospitality industry, including booking systems and customer experience tools.",
    icon: <Hotel sx={{ fontSize: 40, color: "#0c5c4c" }} />, // Slightly smaller icon for Hospitality
  },
  {
    title: "Local Businesses",
    description:
      "We help local businesses increase their online visibility and grow their customer base with tailored solutions.",
    icon: <Storefront sx={{ fontSize: 40, color: "#4a4a4a" }} />, // Slightly smaller icon for Local Businesses
  },
  {
    title: "Startups",
    description:
      "Our startup solutions provide the scalability and flexibility you need to turn your ideas into successful ventures.",
    icon: <Business sx={{ fontSize: 40, color: "#11998e" }} />, // Slightly smaller icon for Startups
  },
  {
    title: "Healthcare",
    description:
      "We build digital solutions for the healthcare sector, improving patient care and operational efficiency.",
    icon: <LocalHospital sx={{ fontSize: 40, color: "#38ef7d" }} />, // Slightly smaller icon for Healthcare
  },
];

const IndustriesWeServe = () => {
  return (
    <Box sx={{ py: 8, backgroundColor: "#f0fdf9" }}>
      <Container>
        <Grid container spacing={4}>
          {industries.map((industry, index) => (
            <Grid item xs={12} md={4} key={index}>
              <motion.div
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.7, delay: index * 0.3 }}
              >
                <Paper
                  sx={{
                    p: 2, // Reduced padding to remove extra space
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "center", // Centers the content vertically
                    minHeight: 200, // Slightly reduced min-height
                    borderRadius: 4,
                    boxShadow: 4,
                    backgroundColor: "#ffffff",
                    transition: "transform 0.3s, box-shadow 0.3s",
                    "&:hover": {
                      transform: "translateY(-10px)",
                      boxShadow: "0px 15px 30px rgba(0, 0, 0, 0.15)",
                    },
                  }}
                >
                  <Box sx={{ mb: 1 }}>
                    {industry.icon} {/* Adding the icon with reduced size */}
                  </Box>
                  <Typography
                    variant="h6"
                    align="center"
                    sx={{
                      fontWeight: "bold",
                      color: "#0c5c4c",
                      mb: 1, // Reduced margin between title and description
                      fontFamily: "'Roboto', sans-serif",
                    }}
                  >
                    {industry.title}
                  </Typography>
                  <Typography
                    variant="body2"
                    align="center"
                    sx={{
                      color: "#4a4a4a",
                      fontFamily: "'Roboto', sans-serif",
                    }}
                  >
                    {industry.description}
                  </Typography>
                </Paper>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default IndustriesWeServe;
